@charset "UTF-8";
/**
 * font-size調整
 */
html {
  font-size: 62.5%;
  /*ベースを10pxにしています*/
}

body {
  font-size: 16px;
  font-size: 1.6rem;
  /*16px*/
}

.card {
  padding: 10px;
  padding: 1rem;
}

.card .menu-icon {
  font-size: 40px;
  font-size: 4rem;
  text-align: center;
  padding: 5px 0 0;
  padding: .5rem 0 0;
}

.card .menu-title {
  font-size: 10px;
  font-size: 1rem;
  text-align: center;
  padding: 5px 0 10px;
  padding: .5rem 0 1rem;
}
